<template>
  <el-container class="freshmen-zone">
    <el-main>
      <el-row justify="center">
        <el-col :span="12" :xs="20" :sm="18" :md="14" :lg="12" :xl="10">
          <el-card class="survey-section card-modern">
            <h3>关于本网站的说明</h3>
            <p>本网站致力于提供全面、真实的课程评价数据，帮助学生更好地选择课程。我们珍视您的反馈，并将继续努力改进我们的服务。</p>
          </el-card>

          <el-card class="survey-section card-modern">
            <h3>调查问卷</h3>
            
            <el-form @submit.prevent="submitSurvey" label-position="top" class="survey-form">
              <el-form-item label="您是否了解过大学的选课体系" required>
                <el-select v-model="surveyData.satisfaction" placeholder="请选择">
                  <el-option label="是" :value="true"></el-option>
                  <el-option label="否" :value="false"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="您希望学生社区能为选课带来怎样的帮助">
                <el-input
                  type="textarea"
                  v-model="surveyData.suggestions"
                  rows="4"
                  placeholder="请输入您的建议..."
                ></el-input>
              </el-form-item>

              <el-form-item>
                <el-button type="primary" @click="submitSurvey" round size="large" class="submit-button">
                  提交问卷
                </el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
import axios from 'axios';

export default {
  name: 'FreshmenZone',
  data() {
    return {
      surveyData: {
        satisfaction: null, // 是否满意
        suggestions: '' // 用户建议
      }
    };
  },
  methods: {
    async submitSurvey() {
      const apiUrl = process.env.NODE_ENV === 'development'
        ? 'http://127.0.0.1:5000/add_survey'
        : 'http://103.20.220.93:5000/add_survey';

      // 构造要提交的JSON数据格式
      const data = {
        satisfaction: this.surveyData.satisfaction,
        suggestions: this.surveyData.suggestions,
        timestamp: new Date().toISOString() // 添加时间戳
      };

      try {
        // 将问卷数据提交到后端
        await axios.post(apiUrl, data);
        
        // 通知父组件提交已完成
        await this.$emit('submitSurvey', this.surveyData);
        this.$emit('surveyCompleted'); // 通知父组件问卷已完成
      } catch (error) {
        console.error('Error submitting survey:', error);
      }
    }
  }
};
</script>

<style scoped>
.freshmen-zone {
  padding: 20px;
  background-color: #f5f7fa;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.survey-section {
  margin-bottom: 20px;
  background-color: #ffffff;
}

.card-modern {
  border-radius: 12px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.survey-form {
  margin-top: 20px;
}

.submit-button {
  background-color: #3498db;
  border-color: #3498db;
  width: 100%;
  border-radius: 20px;
}

.submit-button:hover {
  background-color: #2980b9;
  border-color: #2980b9;
}

h3 {
  margin-top: 0;
  color: #333;
}

.el-card {
  padding: 20px;
}
</style>
